
import { getEnv, Instance, types, flow, getRoot, onSnapshot } from 'mobx-state-tree';

import { User, IUser } from '../models/user';
import { Environment } from './environment';
import * as ApiTypes from '../services/api/api.d';

const _log = require('debug')('app:store:user');

export const UserStoreModel = types
  .model('UserStore', {
    identifier: types.optional(types.identifier, 'UserStore'),
    user: types.maybeNull(User),
    state: types.optional(types.enumeration("State", ["done", "pending", "error"]), 'done'),
  })
  .views(self => ({
    getUser() {
      return self.user;
    },
    isLoggedIn() {
      return (self.user !== null);
    },
  }))
  .actions((self) => {
    const fromStrApi = ({user, jwt}: {user: ApiTypes.RawUser; jwt: string}) => ({
      id: user._id || user.id,
      confirmed: user.confirmed,
      email: user.email,
      username: user.username,
      provider: user.provider,
      token: jwt,
      role: user.role.name,
      //rolename: user.role.name
    });

    return {
      setUser(userJson: IUser) {
        const user = User.create(userJson);
        self.user = user;
        // set api credential
        if (user.token) {
          const env = getEnv(self);
          env.api.apiUser.setCredential(user.token);
        }

        return user;
      },

      setUsername(value: string) {
        if (!self.user) return;
        self.user.username = value;
      },

      clear() {
        self.user = null;
      },

      //@ts-ignore
      register: flow(function*(userRegisterInfo: ApiTypes.UserLocalRegister) { // <- note the star, this a generator function!
        const env: Environment = getEnv(self);
        const { api: { apiUser } } = env;

        if (!apiUser) {
          return {kind: 'invalid-state'};
        }

        self.state = "pending";
        try {
          // ... yield can be used in async/await style
          const rep: ApiTypes.GetUserResult = yield apiUser.auth_local_register(userRegisterInfo);
          _log('>>> register: ', rep.kind);
          
          if (rep.kind === 'ok') {
            //@ts-ignore
            self.setUser(rep.user);
            //eslint-disable-next-line
            self.state = 'done';
          } else {
            console.error(rep);
            //eslint-disable-next-line
            self.state = 'error';
          }
          return rep;
        } catch (e) {
          console.error(e);
        }

        self.state = 'error';
        return { kind: "unknown", temporary: true };
      }),

      //@ts-ignore
      signin: flow(function*(userSignInInfo: ApiTypes.UserLocalLogin) { // <- note the star, this a generator function!
        const env: Environment = getEnv(self);
        const { api: { apiUser } } = env;
      
        if (!apiUser) {
          return {kind: 'invalid-state'};
        }

        self.state = "pending";
        try {
          // ... yield can be used in async/await style
          const rep: ApiTypes.GetUserResult = yield apiUser.auth_local_login(userSignInInfo);
          _log('>>> signin: ', rep.kind);

          if (rep.kind === 'ok') {
            // @ts-ignore
            self.setUser(fromStrApi(rep));
            //eslint-disable-next-line
            self.state = 'done';
          } else {
            console.error(rep);
            //eslint-disable-next-line
            self.state = 'error';
          }
          return rep;
        } catch (e) {
          console.error(e);
        }

        self.state = 'error';
        return { kind: "unknown", temporary: true };
      }),

      signout() {
        //@ts-ignore
        self.clear();
      },

      getApi() {
        const { api: { apiUser } } = getEnv(self);
        return apiUser;
      },

      afterCreate() {
        onSnapshot(self, () => {
          _log('>>> onSnapshot');
          const rootStore = getRoot(self);
          rootStore.save();
        });
      },
    };
  });

export type UserStore = Instance<typeof UserStoreModel>;
