import { Instance, types, getSnapshot } from 'mobx-state-tree';

import { UserStoreModel } from './user-store';

//import { NavigationStoreModel } from '../navigation-store';
//import { ArticleStoreModel } from '../article-store';
//import { FBUserStoreModel } from '../fb-user-store';
//import { GitHubRepoStoreModel, GitHubUserStoreModel } from 'carbon-gh-repo';

import { saveString } from '../utils/storage';

export const RootStoreModel = types
  .model('RootStore', {
    identifier: types.optional(types.identifier, 'RootStore'),

    userStore:  types.optional(UserStoreModel, () => UserStoreModel.create()),

//    fbUserStore: types.optional(FBUserStoreModel, () => FBUserStoreModel.create()),
//    navigationStore: types.optional(NavigationStoreModel, () => NavigationStoreModel.create()),
//    articleStore: types.optional(ArticleStoreModel, () => ArticleStoreModel.create()),
  })
  .actions(self => ({
    async save() {
      try {
        const transformedSnapshot = getSnapshot(self);
        const json = JSON.stringify(transformedSnapshot);
        await saveString('appStatePersistenceKey', json);
      } catch (err) {
        console.warn('unexpected error ' + err);
      }
    },
  }));

export type RootStore = Instance<typeof RootStoreModel>;
