import { types, SnapshotIn } from 'mobx-state-tree';

const { maybeNull } = types;

export const User = types.model('User', {
  "id": types.identifier, // store id
  "confirmed": types.boolean,
  "email": types.string,
  "username": maybeNull(types.string),
  "provider": maybeNull(types.string),
  "token": maybeNull(types.string),
  "role": maybeNull(types.string),
});

export type IUser = SnapshotIn<typeof User>; // <== {id?: sting, username?: string, ...}

