import "./src/styles/style.scss"
import { setup as setupStore } from './src/stores/get-store';
// !!! shoud import i18n
import './src/i18n/i18n';


const _log = require('debug')('app:bootstrap');

/**
 * Called when the Gatsby browser runtime first starts.
 */
export const onClientEntry = () => {
  _log('STARTED...');

  // setup root store
  setupStore();

  window.deferredInstallPrompt = undefined;

  window.addEventListener('beforeinstallprompt', (e) => {
    // Prevent Chrome 76 and later from showing the mini-infobar
    e.preventDefault();
  
    // Stash the event so it can be triggered later.
    window.deferredInstallPrompt = e;
    // Update UI notify the user they can add to home screen
    //showInstallPromotion();
    _log('************************************');
  })
  
  /**
   * To determine if the app was successfully added to the user's home screen 
   * after they accepted the prompt
   */
  window.addEventListener('appinstalled', (evt) => {
    _log("app's been installed");
  });
}

/**
 * Inform plugins when a service worker has become active.
 */
export const onServiceWorkerActive = ({
  serviceWorker
}) => {
  _log('onServiceWorkerActive', serviceWorker);
}

/**
 * Inform plugins when a service worker has been installed.
 */
export const onServiceWorkerInstalled = ({
  serviceWorker
}) => {
  _log('onServiceWorkerInstalled', serviceWorker);
}

/**
 * Inform plugins when a service worker is redundant.
 */
export const onServiceWorkerRedundant = ({
  serviceWorker
}) => {
  _log('onServiceWorkerRedundant', serviceWorker);
}

/**
 * Inform plugins of when a service worker has an update available.
 */
export const onServiceWorkerUpdateFound = ({
  serviceWorker
}) => {
  _log('onServiceWorkerUpdateFound', serviceWorker);
}

/**
 * Inform plugins when a service worker has been updated in the background 
 * and the page is ready to reload to apply changes.
 */
export const onServiceWorkerUpdateReady = ({
  serviceWorker
}) => {
  _log('onServiceWorkerUpdateReady', serviceWorker);
  const answer = window.confirm(`This application has been updated. Reload to display the latest version?`);
  if (answer === true) {
    window.location.reload();
  }
}

/**
 * Allow a plugin to wrap the root element.
 */
export const wrapRootElement = ({
  element
}) => {
  _log('wrapRootElement');
}

