import { ApisauceInstance } from "apisauce"

//import { getGeneralApiProblem, ApiConfig } from "./api-base";
import { ApiConfig } from './api-config';
import { getGeneralApiProblem } from './api-base/api-problem';

import * as Types from "./api.d";

const _log = require('debug')('app:api:api-user');

/**
 * Manages all requests to the API.
 */
export class ApiUser {
  /**
   * The underlying apisauce instance which performs the requests.
   */
  apisauce: ApisauceInstance|null;

  config: ApiConfig;

  /**
   * Creates the user api.
   */
  constructor(apisauce: ApisauceInstance|null, config: ApiConfig) {
    this.apisauce = apisauce;
    this.config = config;
  }

  setCredential(token: string) {
    if (!this.apisauce) return;

    if (token) {
      this.apisauce.setHeader('Authorization', `Bearer ${token}`);
    } else {
      delete this.apisauce.headers['Authorization'];
    }
  }

  getForgetPasswordUrl() {
    return `${this.config.url}/`
  }

  _convertUser(raw: any) {
    return {
      ...raw.user,
      id: `${raw.user.id}`,
      confirmed: !!raw.user.confirmed,
      token: raw.jwt
    };
  };

  /**
   * register user.
   */
  async auth_local_register(userRegisterInfo: Types.UserLocalRegister): Promise<Types.GetUserResult> {
    if (!this.apisauce) return {kind: 'invalid-state'};

    // make the api call
    const response = await this.apisauce.post(
      `/auth/local/register`,
      userRegisterInfo
    );

    // the typical ways to die when calling an api
    if (!response.ok) {
      _log('!!! auth_local_register', response);
      const problem = getGeneralApiProblem(response)
      return problem || {kind: 'canceled'};
    }

    // transform the data into the format we are expecting
    try {
      _log('>>> auth_local_register', response);
      const rawUser = response.data; // <== {jwt: '', user: {}}
      const resultUser = this._convertUser(rawUser);
      this.setCredential(resultUser.token);
      return { kind: "ok", user: resultUser }
    } catch (e) {
      console.error(e);
      return { kind: "bad-data" }
    }
  }

  async auth_local_login(userLoginInfo: Types.UserLocalLogin): Promise<Types.GetUserResult> {
    _log('userLoginInfo', userLoginInfo);
    if (!this.apisauce) return {kind: 'invalid-state'};

    // make the api call
    const response = await this.apisauce.post(
      `/auth/local`,
      userLoginInfo
    );

    // the typical ways to die when calling an api
    if (!response.ok) {
      _log('!!! auth_local_login', response);
      const problem = getGeneralApiProblem(response)
      return problem || {kind: 'canceled'};
    }

    // transform the data into the format we are expecting
    try {
      _log('>>> auth_local_login', response);
      const rawUser = response.data; // <== {jwt: '', user: {}}
      const resultUser = this._convertUser(rawUser);
      // login success.
      // set authorization header for later use
      this.setCredential(resultUser.token);
      return { kind: "ok", user: resultUser }
    } catch (e) {
      console.error(e);
      return { kind: "bad-data" }
    }
  }

  async users_me(token: string|null = null): Promise<Types.GetUserResult> {
    if (!this.apisauce) return {kind: 'invalid-state'};

    // make the api call
    const response = !token ? await this.apisauce.get(`/users/me`)
      : await this.apisauce.get(`/users/me`, {}, {headers: {Authorization: `Bearer ${token}`}});

    // the typical ways to die when calling an api
    if (!response.ok) {
      _log('!!! users_me', response);
      const problem = getGeneralApiProblem(response)
      return problem || {kind: 'canceled'};
    }

    // transform the data into the format we are expecting
    try {
      _log('>>> users_me', response);
      const rawUser = response.data; // <== {user: {}}
      const resultUser = this._convertUser(rawUser);
      return { kind: "ok", user: resultUser }
    } catch (e) {
      console.error(e);
      return { kind: "bad-data" }
    }
  }

  /**
   * Sends an email to a user with the link of your reset password page.
   * This link contains an URL param code which is required to reset user password.
   * Received link url format https://my-domain.com/rest-password?code=privateCode.
   * @param email
   * @param url Link that user will receive.
   */
  async auth_forgot_password(email: string, url: string): Promise<Types.GeneralApiResponse> {
    if (!this.apisauce) return {kind: 'invalid-state'};

    this.setCredential(''); // remove credential

    const response = await this.apisauce.post(
      `/auth/forgot-password`,
      {email, url}
    );

    // the typical ways to die when calling an api
    if (!response.ok) {
      _log('!!! auth_forgot_password', response);
      const problem = getGeneralApiProblem(response)
      return problem || {kind: 'canceled'};
    }
    
    return { kind: "ok" };
  }

  /**
   * Reset the user password.
   * @param code Is the url params received from the email link (see forgot password).
   * @param password
   * @param passwordConfirmation
   */
  async auth_reset_password(code: string, password: string, passwordConfirmation: string): Promise<Types.GeneralApiResponse> {
    if (!this.apisauce) return {kind: 'invalid-state'};

    this.setCredential(''); // remove credential
    
    const response = await this.apisauce.post(
      `/auth/reset-password`,
      {code, password, passwordConfirmation}
    );

    // the typical ways to die when calling an api
    if (!response.ok) {
      _log('!!! auth_reset_password', response);
      const problem = getGeneralApiProblem(response)
      return problem || {kind: 'canceled'};
    }
    
    return { kind: "ok"};
  }

  /**
   * Change the user password.
   * @param oldPassword
   * @param newPassword
   * @param newPasswordConfirmation
   */
  async auth_change_password(oldPassword: string, newPassword: string, newPasswordConfirmation: string): Promise<Types.GeneralApiResponse> {
    if (!this.apisauce) return {kind: 'invalid-state'};
    
    const response = await this.apisauce.post(
      `/auth/change-password`,
      {oldPassword, newPassword, newPasswordConfirmation}
    );

    // the typical ways to die when calling an api
    if (!response.ok) {
      _log('!!! auth_change_password', response);
      const problem = getGeneralApiProblem(response)
      return problem || {kind: 'canceled'};
    }
    
    return { kind: "ok"};
  }

  /**
   * Retrieve the connect provider URL
   * @param provider
   */
  getProviderAuthenticationUrl(provider: string) {
    return `${this.config.url}/connect/${provider}`;
  }

}
