import { onSnapshot } from "mobx-state-tree"

import { createEnvironment } from './environment';
import { RootStoreModel } from "./root-store"
import * as storage from '../utils/storage';

const _log = require('debug')('app:store:get');

/**
 * The key we'll be saving our state as within async storage.
 */
const ROOT_STATE_STORAGE_KEY = "_root_"

function loadFromStorage() {
  try {
    // load data from storage
    return storage.load(ROOT_STATE_STORAGE_KEY);
  } catch(e) {
    console.error(e);
    return null;
  }
}

function createRootStore() {
  let rootStore: any = null;
  let data;

  try {
    // 초기에 rootStore를 생성할 때, 
    // empty object({})로 rootStore 생성하면 (RootStoreModel.create({}, env)),
    // getEnv()가 null을 리턴함
    // workaround:
    // storage에서 로드한 값이 비어 있는 경우에는
    // default 값으로 root store를 생성하고, 바로 저장한 뒤,
    // 다시 storage에서 로드해서 rootStore를 생성함
    data = loadFromStorage();
    if (!data) {
      // create root store with default and save it
      storage.save(ROOT_STATE_STORAGE_KEY, RootStoreModel.create());
      data = loadFromStorage();
    }
    _log('>>> createRootStore: stored data', data);

    // create root store with env
    const env = createEnvironment();
    rootStore = RootStoreModel.create(data, env);

    _log('*** createRootStore: CHECK API CREDENTIAL ***');
    //if (rootStore.userStore.user) {
    //  env.api.apiUser.setUser(rootStore.userStore.user);
    //}

    // track changes & save to storage
    onSnapshot(rootStore, snapshot => storage.save(ROOT_STATE_STORAGE_KEY, snapshot));
  } catch(e) {
    console.error(e);
  }

  if (!rootStore) {
    _log('!!! createRootStore: Could not create RootStore...');
    // return null;
  }

  return rootStore;
}

let _storeInstance: any = undefined;

export function setup() {
  _storeInstance = createRootStore();
}

export function getStore() {
  if (!_storeInstance) _storeInstance = createRootStore();

  return _storeInstance;
}

export function getUserStore() {
  const {userStore} = getStore();
  return userStore;
}
